	export default {
		data() {
			return {
				classifyArray:[]
			}
		},
		methods: {
			toHome(index){
                uni.switchTab({
                    url:"/pages/tarbar/home/home"
                })
                this.$store.commit('changeTar',index)
            }
		},
        onLoad() {
            uni.request({
                url: this.websiteUrl + '/api/home/moretype',
                method: 'GET',
                data: {},
                success: res => {
                    this.classifyArray = res.data;
                },
                fail: () => {},
                complete: () => {}
            });
        }
	}
